export default {
	data() {
		return {
			loadingText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			},
			scrollLeft: 0,
			isClickChange: false,
			tabIndex: 0,
			newsitems: [],
			tabBars: []
		}
	},
	onLoad: function() {
		var _this = this;
		_this.$Request.post('article').then(res => {
			if (res.code == 200) {
				console.log(res);
				_this.tabBars = res.datas.article_class;
				_this.newsitems = res.datas.list;
				_this.tabIndex = res.datas.index_id;
			}
		});
	},
	methods: {
		goDetail(e) {
			uni.navigateTo({
				url: '/pages/index/newsdetail?aid=' + e.article_id
			})
		},
		
		async changeTab(e) {
			console.log(e);
		},
		getElSize(id) { //得到元素的size
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select('#' + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					res(data);
				}).exec();
			});
		},
		async tapTab(index) { 
			let _this = this;
			if (this.tabIndex === index) {
				return false;
			} else {
				let tabBar = await this.getElSize("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.tabIndex = index;
				_this.$Request.post('article/article_list',{ac_id:index}).then(res => {
					if (res.code == 200) {
						console.log(res);
						_this.newsitems = res.datas.article_list;
					}
				});
			}
		}
	}
}
